@import "https://fonts.googleapis.com/css?family=Josefin+Sans:400,700";

body {
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  background-color: #eee;

  a {
    color: #ffffff;
    text-decoration: none;
  }
}

.card {
  position: relative;
  top: 50%;
  left: 50%;
  height: 400px;
  width: 300px;
  transform: translate(-50%, -50%);
  transform-style: preserve-3d;
  perspective: 600px;
  transition: 0.5s;

  &:hover {
    .card-front {
      transform: rotateX(-180deg);
    }

    .card-back {
      transform: rotateX(0deg);
    }
  }
}

.card-front {
  height: 100%;
  width: 100%;
  background-image: url(../Assets/5856.jpg);
  background-position: 50% 50%;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #e4d7d7;
  backface-visibility: hidden;
  transform: rotateX(0deg);
  transition: 0.5s;
}

.card-back {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
  backface-visibility: hidden;
  transform: rotateX(180deg);
  transition: 0.5s;
  color: black;
  text-align: center;

  h2 {
    margin: 60% auto 35% auto;
    font-size: 26px;

    span {
      font-size: 20px;
    }
  }

  a {
    height: 20px;
    width: 20px;
    padding: 5px 5px;
    border-radius: 4px;
    line-height: 20px;

    &:hover {
      color: #000000;
      background-color: #fff;
    }
  }
}
